import { useEffect } from 'react';
import { Link } from '@mui/material';
import { Redirect, useLocation, Link as RouterLink } from 'react-router-dom';
import useAuth from 'src/hooks/useAuth';
import Page from 'src/components/Page';
import { useDispatch } from 'src/store';
import api from 'src/utils/api';

const logo = require('src/assets/images/verify-success.png');

const style = {
  footer: {
    width: '100%',
    objectFit: 'contain'
  }
}

const SignUpVeriyView = (props) => {
  const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    async function verifyAccount() {
      const token = new URLSearchParams(props.location.search).get("token")
      // dispatch(verifyAccount(token).then(() => {}));
      const response = await api.post(`/user/sign-up/verify/${token}`);
      // history.push('/vleppo/login');
    }
    verifyAccount();
  }, []);

  const { isAuthenticated } = useAuth();

  const system = location.pathname.split('/')[1];
  if (isAuthenticated) {
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');

    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}`} />;
  }

  return (
    <Page title="Verify account">
      <Link component={RouterLink} to="/vleppo/login">
        <img style={style.footer} src={logo} alt="verify-success.png" />
      </Link>
      {/* <Grid container sx={{ height: '100vh' }}>
        <Grid item xs={12} sx={{ background: '#E5E5E5', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          Sign up verify
        </Grid>
      </Grid> */}
    </Page>
  );
};

export default SignUpVeriyView;
